html,
body {
	margin: 0;
	height: 100%;
	font-family: "Open Sans";
	position: relative;
}


.App-logo {
	pointer-events: none;
	margin: 20px;
}
@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: swing 4s infinite ease-in-out;
		height: 10vh;
		width: auto;
	}
}

@keyframes swing {
	0%,100% {
		transform: rotate(-30deg);
	}
	20% {
		transform: scale(0.95);
	}
	50% {
		transform: rotate(30deg);
	}
	80% {
		transform: scale(0.95);
	}
}

iframe {
	width: 100%;
	height: 100vh;
	overflow: scroll;
	padding-bottom: 5%;
}

img {
	max-width: 100%;
}

.App-header {
	background-color: #282c34;
	top: 0;
	min-height: 10vh;
	font-size: calc(5px + 2vmin);
	color: white;
	text-align: center;
	position: sticky;
	z-index: 999;
}

.App-body {
	margin: 5vh 5vw;
	max-height: 100%;
	padding-bottom: 5%;
}

footer {
	background-color: #282c34;
	bottom: 0;
	width: 100%;
	position: fixed;
	font-size: calc(5px + 2vmin);
	text-align: center;
}

